<template>
  <div class="account-box">
    <!-- v-model -->
    <Form class="form" :validation-schema="rules" v-slot="{ errors }" ref="fm">
      <!--  null-->
      <!-- <p>{{ errors }}</p> -->
      <!--  -->
      <div class="form-item">
        <div class="input">
          <i class="iconfont icon-user"></i>
          <Field
            type="text"
            placeholder=""
            v-model="FormData.account"
            name="account"
            :class="{ error: errors.account }"
          />
        </div>
        <!--  -->
        <div
          v-if="errors.account"
          class="error"
          :class="{ errtips: errors.account }"
        >
          <i class="iconfont icon-warning" />
        </div>
      </div>
      <div class="form-item">
        <div class="input">
          <i class="iconfont icon-lock"></i>
          <Field
            type="password"
            placeholder=""
            v-model="FormData.password"
            name="password"
            :class="{ error: errors.password }"
          />
        </div>
        <!--  -->
        <div
          v-if="errors.password"
          class="error"
          :class="{ errtips: errors.password }"
        >
          <i class="iconfont icon-warning" />
        </div>
      </div>
      <div class="form-item">
        <div class="agree">
          <!--  -->
          <!-- <XtxCheckbox v-model="FormData.isAgree"/> -->
          <!-- Field as -->
          <Field v-model="FormData.isAgree" as="XtxCheckbox" name="isAgree">
            <span></span>
          </Field>
          <a href="javascript:;"></a>
          <span></span>
          <a href="javascript:;"></a>
        </div>
        <!--  -->
        <div
          v-if="errors.isAgree"
          class="error"
          :class="{ errtips: errors.isAgree }"
        >
          <i class="iconfont icon-warning" />{{ errors.isAgree }}
        </div>
      </div>
      <a href="javascript:;" class="btn" @click="submit"></a>
    </Form>
    <div class="action">
      <img
        src="https://qzonestyle.gtimg.cn/qzone/vas/opensns/res/img/Connect_logo_7.png"
        alt=""
      />
      <div class="url">
        <a href="javascript:;"></a>
        <a href="javascript:;"></a>
      </div>
    </div>
  </div>
</template>
<script>
// vee-validateelementel-Form
import { Form, Field } from 'vee-validate'
import { reactive, ref } from 'vue'
// 
import rulesFns from '@/utils/vee-validate-schema'
import { useStore } from 'vuex'
import msg from '@/components/Message'
import { useRoute, useRouter } from 'vue-router'
// 
export default {
  /**
   *  
   * 
   * formfield
   */
  components: {
    // 
    Form,
    // input
    Field
  },
  setup () {
    // router
    const router = useRouter()
    // 
    const route = useRoute()
    // store
    const store = useStore()
    // 
    const fm = ref(null)
    // 
    const FormData = reactive({
      account: 'dijia', // 
      password: '123456', // 
      // false
      isAgree: false
    })
    // ---- rules
    const rules = reactive({
      account: rulesFns.account,
      password: rulesFns.password,
      isAgree: rulesFns.isAgree
    })
    // 1. input Field input
    // 2. Field name()
    // 3. Fieldv - model
    // 4. errorerrors

    // 
    // 1. ref
    // 2. validate promise

    const submit = async () => {
      // await
      // 
      // console.log('loading', fm.value.validate())
      const { valid } = await fm.value.validate() // valid
      // console.log(res)
      if (valid) {
        // 
        // console.log('loading')
        try {
          // ref.value
          await store.dispatch('user/getUser', FormData)
          msg({ type: 'success', text: '' })
          // ()
          store.dispatch('cart/mergeLocalCartActions')
          // router---
          router.replace(route.query.redirectUrl || '/')
        } catch (error) {
          console.dir(error)
          msg({ type: 'error', text: error.response.data.message })
        }
      }
    }
    return { FormData, rules, fm, submit }
  }
}
</script>
<style lang="less" scoped>
.errtips {
  color: #e40438;
  animation-name: errtips;
  animation-duration: 0.5s;
}
@keyframes errtips {
  10% {
    transform: translateY(1px);
  }
  20% {
    transform: translateY(-1px);
  }
  30% {
    transform: translateY(1px);
  }
  40% {
    transform: translateY(-1px);
  }
  50% {
    transform: translateY(1px);
  }
  60% {
    transform: translateY(-1px);
  }
  70% {
    transform: translateY(1px);
  }
  80% {
    transform: translateY(-1px);
  }
  90% {
    transform: translateY(1px);
  }
  100% {
    transform: translateY(0);
  }
}

// 
// 
.account-box {
  .toggle {
    padding: 15px 40px;
    text-align: right;
    a {
      color: @xtxColor;
      i {
        font-size: 14px;
      }
    }
  }
  .form {
    padding: 0 20px;
    &-item {
      margin-bottom: 28px;
      .input {
        position: relative;
        height: 36px;
        > i {
          width: 34px;
          height: 34px;
          background: #cfcdcd;
          color: #fff;
          position: absolute;
          left: 1px;
          top: 1px;
          text-align: center;
          line-height: 34px;
          font-size: 18px;
        }
        input {
          padding-left: 44px;
          border: 1px solid #cfcdcd;
          height: 36px;
          line-height: 36px;
          width: 100%;
          &.error {
            border-color: @priceColor;
          }
          &.active,
          &:focus {
            border-color: @xtxColor;
          }
        }
        .code {
          position: absolute;
          right: 1px;
          top: 1px;
          text-align: center;
          line-height: 34px;
          font-size: 14px;
          background: #f5f5f5;
          color: #666;
          width: 90px;
          height: 34px;
          cursor: pointer;
        }
      }
      > .error {
        position: absolute;
        font-size: 12px;
        line-height: 28px;
        color: @priceColor;
        i {
          font-size: 14px;
          margin-right: 2px;
        }
      }
    }
    .agree {
      a {
        color: #069;
      }
    }
    .btn {
      display: block;
      width: 100%;
      height: 40px;
      color: #fff;
      text-align: center;
      line-height: 40px;
      background: @xtxColor;
      &.disabled {
        background: #cfcdcd;
      }
    }
  }
  .action {
    padding: 20px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .url {
      a {
        color: #999;
        margin-left: 10px;
      }
    }
  }
}
</style>
