
/**
 * 
 * validation-schema :   rules
 * v-slot=>
 */
export default {
  // account
  /**
   *
   * @param {*} value 
   * @returns  | true
   */
  account (value) {
    if (!value) return ''
    return true
  },
  password (value) {
    if (!value) return ''
    if (!/^\w{6,24}$/.test(value)) return '6-24'
    return true
  },
  isAgree (value) {
    if (!value) return ''
    return true
  }
}
